* {
    font-family: 'Inter', sans-serif;
    box-sizing: border-box;
}

body {
    display: flex;
    background-color: #282D35;
    margin: 0;
    height: 100vh;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.main {
    width: 550px;
    height: 550px;
    background-color: #9be2b1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 9px;
}

.description {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 60%;
}

.description>h1 {
    margin: 0;
    color: #1b293d;
    margin-bottom: 10px;
}

.description>p {
    margin: 0;
    text-align: center;
    color: #1b293d;
}

.dice--container {
    padding: 30px 0;
    display: grid;
    grid-template: auto auto / repeat(5, 1fr);
    gap: 30px;
}

.dice {
    width: 50px;
    height: 50px;
    border-radius: 9px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    -webkit-box-shadow: 5px 7px 15px -1px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 5px 7px 15px -1px rgba(0, 0, 0, 0.75);
    box-shadow: 5px 7px 15px -1px rgba(0, 0, 0, 0.75);
}

.dice--number {
    text-align: center;
    font-weight: 600;
    font-size: 25px;
    color: #211039;
    cursor: pointer;
}

button {
    width: 100px;
    height: 40px;
    background-color: #482383;
    border: 0px;
    border-radius: 9px;
    cursor: pointer;
    color: white;
    font-size: 20px;
    font-weight: 600;

    -webkit-box-shadow: 5px 7px 15px -1px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 5px 7px 15px -1px rgba(0, 0, 0, 0.75);
    box-shadow: 5px 7px 15px -1px rgba(0, 0, 0, 0.75);
}

button:active {
    box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

